.thumbnail-wrapper {
    width: 340px;
    height: 340px;
    background-image: linear-gradient(white, black 100%);
    position: relative;
    margin: 25px;
    border-radius: 10px;
    cursor: pointer;
}

.thumbnail-title {
    color: white;
    font-size: 20px;
    font-family: "Montserrat", sans-serif;
    position: absolute;
    bottom: 10%;
    max-width: 300px;
    margin: 0 20px;
}
.thumbnail-picture {
    width: 340px;
    height: 340px;
    object-fit: cover;
    border-radius: 10px;
}

@media (max-width: 720px) {
    .thumbnail-wrapper {
        margin: 20px;
    }
    .thumbnail-picture,
    .thumbnail-wrapper {
        width: 335px;
        height: 255px;
    }
}
